import * as React from 'react';
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import SignUp from './registerTabs';
import SignIn from './loginTabs';
// import Link from '@mui/material/Link';
// import Grid from '@mui/material/Grid';

export default function LoginRegister(props) {
    const [value, setValue] = React.useState('1');

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <section id="register" style={{ paddingTop: '125px', paddingBottom: '75px', display: 'flex' }}>
            {/* <Box sx={{ width: '25%', typography: 'body1' }}>
            </Box> */}
            <Box sx={{ width: '500px', typography: 'body1', border: '3px black solid', borderRadius: '25px', margin: 'auto' }}>
                <TabContext value={value}>
                    <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                        <TabList sx={{}} onChange={handleChange} aria-label="lab API tabs example">
                            <Tab sx={{ width: '50%', maxWidth: 'none' }} label="Register" value="2" />
                            <Tab sx={{ width: '50%', maxWidth: 'none' }} label="Login" value="1" />
                        </TabList>
                    </Box>
                    <TabPanel value="2">
                        <SignUp />
                        {/* <Grid container justifyContent="flex-end">
                            <Grid item>
                                <Link href="#" variant="body2">
                                    Already have an account? Sign in
                                </Link>
                            </Grid>
                        </Grid> */}
                    </TabPanel>
                    <TabPanel value="1">
                        <SignIn login={props.logUserIn}/>
                        {/* <Grid container>
                            <Grid item xs>
                                <Link href="#" variant="body2">
                                    Forgot password?
                                </Link>
                            </Grid>
                            <Grid item>
                                <Link href="#" variant="body2">
                                    {"Don't have an account? Sign Up"}
                                </Link>
                            </Grid>
                        </Grid> */}
                    </TabPanel>
                </TabContext>
            </Box>
            {/* <Box sx={{ width: '25%', typography: 'body1' }}>
            </Box> */}
        </section>
    );

}
