import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import axios from 'axios'
import 'aos/dist/aos.css';
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
class Destination extends Component {
    constructor(props) {
        super(props);
        this.state = {
            tourhd: [],
            loadinghd: false,
            tour1d: [],
            loading1d: false,
            tour2d: [],
            loading2d: false,

        }
    };
    getHalfDayTour() {
        this.setState({ loadinghd: true })
        axios.post('/api/maintour/hd')
            .then(res => {
                const tourhd = res.data;
                this.setState({ tourhd: tourhd.tourhd, loadinghd: false }
                );
            })
            .catch((error) => {
                console.log(error)

            })

    }
    getOneDayTour() {
        this.setState({ loading1d: true })
        axios.post('/api/maintour/1d')
            .then(res => {
                const tour1d = res.data;
                this.setState({ tour1d: tour1d.tour1d, loading1d: false }
                );
            })
            .catch((error) => {
                console.log(error)

            })

    }
    getTwoDayTour() {
        this.setState({ loading2d: true })
        axios.post('/api/maintour/2d')
            .then(res => {
                const tour2d = res.data;
                this.setState({ tour2d: tour2d.tour2d, loading2d: false }
                );
            })
            .catch((error) => {
                console.log(error)

            })

    }
    componentDidMount() {
        this.getHalfDayTour();
        this.getOneDayTour();
        this.getTwoDayTour();
    }
    componentDidUpdate() {
    }
    render() {
        console.log("tour2d: ", this.state.tour2d)
        // if (this.state.blog.length === 0) {
        //     return (
        //         <section id="tour" style={{ marginTop: '10%' }}>
        //             <NotFound></NotFound>
        //         </section >
        //     )
        // }
        // console.log("this.state.loading:", this.state.loading)
        // if (this.state.loading === true) {
        //     return (
        //         <section id="tour" style={{ paddingTop: '10%', paddingBottom: '10%' }}>
        //             <h1 style={{ textAlign: "center", fontWeight: 'bold', margin: '5%' }}>Hot Tours</h1>
        //             <Loader></Loader>
        //         </section>)
        // }
        // const settingshd = {
        //     dots: false,
        //     infinite: true,
        //     speed: 500,
        //     slidesToShow: 1,
        //     slidesToScroll: 1,
        //     initialSlide: 0,
        //     arrow: true,
        //     responsive: [
        //         {
        //             breakpoint: 1024,
        //             settings: {
        //                 slidesToShow: 3,
        //                 slidesToScroll: 3,
        //                 infinite: true,
        //                 dots: true
        //             }
        //         },
        //         {
        //             breakpoint: 600,
        //             settings: {
        //                 slidesToShow: 2,
        //                 slidesToScroll: 2,
        //                 initialSlide: 2
        //             }
        //         },
        //         {
        //             breakpoint: 480,
        //             settings: {
        //                 slidesToShow: 1,
        //                 slidesToScroll: 1
        //             }
        //         }
        //     ]
        // }
        return (
            <section id="destination" style={{padding: '20px'}} className="">
                <h1 style={{ textAlign: "center", fontWeight: 'bold', paddingTop: '100px' }}>All tours</h1>
                <Container>
                    <Row>
                        <Col md={4} className="tour_section_title"><span>Tour nửa ngày</span></Col>
                        <Col md={8} className="tour_section_description">Lorem ipsum dolor sit amet
                            consectetur adipisicing elit. Vero dolores est, ipsam dolore aspernatur
                            ex nam distinctio itaque voluptas aliquam quis. Iure reprehenderit
                            eligendi sit repellat magni provident est quae!</Col>
                    </Row>
                    <div className="tour_list">
                        {this.state.tourhd.map(item => (
                            <Link to={`tour/${item.title}`} key={item.id}>
                                <div className="flip">
                                    <div className="front" style={{ backgroundImage: `url(${item.image})` }}>
                                        <h1 className="text-shadow">{item.title}</h1>
                                    </div>
                                    <div className="back">
                                        <h2>Angular</h2>
                                        <p>Good tools make application development quicker and easier to maintain than if you did everything by hand..</p>
                                    </div>
                                </div>
                            </Link>
                        ))}
                    </div>
                    {/* <Slider {...settingshd}>
                        {this.state.tourhd.map(item => (
                            <Link to={`tour/${item.title}`} key={item.id}>
                                <div className="flip">
                                    <div className="front" style={{ backgroundImage: `url(${item.image})` }}>
                                        <h1 className="text-shadow">{item.title}+{item.id}</h1>
                                    </div>
                                    <div className="back">
                                        <h2>Angular</h2>
                                        <p>Good tools make application development quicker and easier to maintain than if you did everything by hand..</p>
                                    </div>
                                </div>
                            </Link>
                        ))}

                    </Slider> */}

                    <Row>
                        <Col md={4} className="tour_section_title"><span>Tour 1 ngày</span></Col>
                        <Col md={8} className="tour_section_description">Lorem ipsum dolor sit amet
                            consectetur adipisicing elit. Vero dolores est, ipsam dolore aspernatur
                            ex nam distinctio itaque voluptas aliquam quis. Iure reprehenderit
                            eligendi sit repellat magni provident est quae!</Col>
                    </Row>
                    <div className="tour_list">
                    {this.state.tour1d.length>=1 ?  this.state.tour1d.map(item => (
                        <Link to={`tour/${item.title}`} key={item.id}>
                            <div className="flip">
                                <div className="front" style={{ backgroundImage: `url(${item.image})` }}>
                                    <h1 className="text-shadow">{item.title}</h1>
                                </div>
                                <div className="back">
                                    <h2>Angular</h2>
                                    <p>{item.description.slice(0, 100)}</p>
                                </div>
                            </div>
                        </Link>
                    )):<h1>ko data</h1>}
                   
                    </div>
                    <Row>
                        <Col md={4} className="tour_section_title"><span>Tour 2 ngày</span></Col>
                        <Col md={8} className="tour_section_description">Lorem ipsum dolor sit amet
                            consectetur adipisicing elit. Vero dolores est, ipsam dolore aspernatur
                            ex nam distinctio itaque voluptas aliquam quis. Iure reprehenderit
                            eligendi sit repellat magni provident est quae!</Col>
                    </Row>
                    <div className="tour_list">
                    {this.state.tour2d.length>=1 ?  this.state.tour2d.map(item => (
                        <Link to={`tour/${item.title}`} key={item.id}>
                            <div className="flip">
                                <div className="front" style={{ backgroundImage: `url(${item.image})` }}>
                                    <h1 className="text-shadow">{item.title}</h1>
                                </div>
                                <div className="back">
                                    <h2>Angular</h2>
                                    <p>{item.description}</p>
                                </div>
                            </div>
                        </Link>
                    )):<h1 style={{marginBottom: '0'}}>ko data</h1>}
                    </div>
                   
                </Container>
            </section>
        )
    }

}
export default Destination