import React, { Component } from 'react'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import AOS from 'aos';
import 'aos/dist/aos.css';
class AboutUs extends Component {
    componentDidMount() {
        AOS.init();
    }
    render() {
        return (
            <section id="about" style={{ paddingTop: '5%' }}>
                <Container fluid>
                    <Row>
                        <Col xs={12} sm={12} md={12} lg={12} xl={6} xxl={6} style={{ margin: "auto" }}>
                            <div data-aos="fade-up" data-aos-delay="1000" data-aos-duration="1500" data-aos-once="true" className="aboutTextBox">
                                <h1>Về chúng tôi</h1>
                                <p>Fresh Tour luôn gắn các giá trị văn hoá miền Tây với mọi hoạt động kinh doanh để tạo ra giá trị cốt lõi cho thương hiệu.</p>
                                <p>Triết lý kinh doanh của Fresh Tour là luôn đề cao sự thoải mái và riêng tư của khách hàng. Luôn mang đến những dịch vụ tốt nhất cho khách hàng trải nghiệm.</p>
                                <p>Fresh Tour chuyên cung cấp các dịch vụ du lịch miền Tây, ngoài ra chúng tôi còn là đối tác chiến lượt của các chuỗi resrots, hotels chất lượng và hệ thống vui chơi của các tập đoàn SunGroup và VinGroup. Chúng tôi có thể dễ dàng xử lý trọn vẹn toàn bộ lộ trình du lịch cho khách hàng, tự hào là doanh nghiệp uy tín và chất lượng.</p>
                            </div>
                        </Col>
                        <Col xs={12} sm={12} md={12} lg={12} xl={6} xxl={6} className="aboutHeight">
                            <div className="aboutImgBox">
                                <div style={{ zIndex: 0, position: "relative" }} data-aos="fade-up" data-aos-delay="100" data-aos-duration="1500" data-aos-once="true"><img className="about1_img" src="images/about1.JPG" alt="aboutUs1" ></img></div>
                                <div style={{ zIndex: 1, position: "relative" }} data-aos="fade-up" data-aos-delay="500" data-aos-duration="1500" data-aos-once="true"> <img className="about2_img" src="images/about2.JPG" alt="aboutUs2" ></img></div>
                                <div style={{ zIndex: 2, position: "relative" }} data-aos="fade-up" data-aos-delay="1000" data-aos-duration="1500" data-aos-once="true"> <img className="about3_img" src="images/about3.JPG" alt="aboutUs3" ></img></div>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </section>
        )
    }

}
export default AboutUs