import React, {useState} from 'react'
import { Link } from 'react-router-dom';
import Avatar from '@mui/material/Avatar';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';
import PersonAdd from '@mui/icons-material/PersonAdd';
import Settings from '@mui/icons-material/Settings';
import Logout from '@mui/icons-material/Logout';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';


export default function Navigation(props) {
    // const [login, setLogin] = useState(false)
    const [anchorEl, setAnchorEl] = React.useState(null);
    // useEffect(() => {
    //     setInterval(() => {
    //         if (localStorage.token && localStorage.token !== "") {
    //             setLogin(true);
    //             console.log("Đăng nhập")
    //         }
    //         else {
    //             setLogin(false);
    //             console.log("Đăng xuất")
    //         }
    //     }, [])


    const open = Boolean(anchorEl);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
    };
    // const logout = () => {
    //     localStorage.removeItem('token')
    // }
    // console.log('props.login', props.isLoginIn)
    return (
        <div className="navbar-section" id="navbar">
            <nav className="navbar navbar-expand-lg navbar-dark bg-brown static-top">
                <div className="container">
                    <a className="navbar-brand" href="/#">
                        <img src="/images/logo.JPG" alt="logo" height="36" />
                    </a>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul id="nav" className="navbar-nav ms-auto">
                            {/* <li className="current nav-item home">
                                <a className="nav-link" aria-current="page" href="/#"><span data-hover="Trang chủ">Trang chủ</span></a>
                            </li> */}
                            <li className="nav-item home">
                                <a className="nav-link" aria-current="page" href="/#"><span data-hover="Trang chủ">Trang chủ</span></a>
                            </li>
                            <li className="nav-item about">
                                <a className="nav-link" href="/#about"><span data-hover="Chúng tôi">Chúng tôi</span></a>
                            </li>
                            <li className="nav-item tour">
                                <a className="nav-link" href="/#tour"><span data-hover="Hot tour">Hot tour</span></a>
                            </li>

                            <li className="nav-item dropdown">
                                <a className="nav-link dropdown-toggle" href="#desitination" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                    <span data-hover="Điểm đến">Điểm đến</span>
                                </a>
                                <ul className="dropdown-menu dropdown-menu-end" aria-labelledby="navbarDropdown">
                                    <li><a className="dropdown-item" href="/tour">Tour nửa ngày</a></li>
                                    <li><a className="dropdown-item" href="/tour">Tour 1 ngày</a></li>
                                    <li><a className="dropdown-item" href="/tour">Tour 2 ngày</a></li>
                                    <li><a className="dropdown-item" href="/tour">Tour 3 ngày</a></li>
                                    <li><a className="dropdown-item" href="/tour">Tour 4 ngày</a></li>
                                    <li><a className="dropdown-item" href="/tour">Tour 5 ngày</a></li>
                                    {/* <li>
                                        <hr className="dropdown-divider" />
                                    </li>
                                    <li><a className="dropdown-item" href="#desitination">Something else here</a></li> */}
                                </ul>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="/blog"> <span data-hover="Travel blogs">Travel blogs</span></a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" href="#footer"><span data-hover="Liên hệ">Liên hệ</span></a>
                            </li>
                            {/* <li className="nav-item">
                                <a className="nav-link" href="/sign"><i class="fas fa-user"></i></a>
                            </li> */}
                            {props.isLoginIn === null && <li className="nav-item">
                                <a className="nav-link" href="/sign"><i className="fas fa-user"></i></a>
                            </li>}
                            {props.isLoginIn != null && <li className="nav-item">
                                <Tooltip title="Account settings">
                                    <IconButton
                                        onClick={handleClick}
                                        size="small"
                                        sx={{ ml: 2 }}
                                        aria-controls={open ? 'account-menu' : undefined}
                                        aria-haspopup="true"
                                        aria-expanded={open ? 'true' : undefined}
                                    >
                                        <Avatar sx={{ width: 40, height: 40 }}>{props.userName}</Avatar>
                                    </IconButton>
                                </Tooltip>
                                <Menu
                                    anchorEl={anchorEl}
                                    id="account-menu"
                                    open={open}
                                    onClose={handleClose}
                                    onClick={handleClose}
                                    PaperProps={{
                                        elevation: 0,
                                        sx: {
                                            overflow: 'visible',
                                            filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
                                            mt: 1.5,
                                            '& .MuiAvatar-root': {
                                                width: 32,
                                                height: 32,
                                                ml: -0.5,
                                                mr: 1,
                                            },
                                            '&:before': {
                                                content: '""',
                                                display: 'block',
                                                position: 'absolute',
                                                top: 0,
                                                right: 14,
                                                width: 10,
                                                height: 10,
                                                bgcolor: 'background.paper',
                                                transform: 'translateY(-50%) rotate(45deg)',
                                                zIndex: 0,
                                            },
                                        },
                                    }}
                                    transformOrigin={{ horizontal: 'right', vertical: 'top' }}
                                    anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
                                >
                                    <MenuItem>
                                        <Avatar /> Profile
                                    </MenuItem>
                                    <MenuItem>
                                        <Avatar /> My account
                                    </MenuItem>
                                    <Divider />
                                    <MenuItem>
                                        <ListItemIcon>
                                            <PersonAdd fontSize="small" />
                                        </ListItemIcon>
                                        Add another account
                                    </MenuItem>
                                    <MenuItem>
                                        <ListItemIcon>
                                            <Settings fontSize="small" />
                                        </ListItemIcon>
                                        Settings
                                    </MenuItem>
                                    <MenuItem
                                        component={Link}
                                        to="/sign"
                                        onClick={props.logUserOut}
                                    >

                                        <ListItemIcon>
                                            <Logout fontSize="small" />
                                        </ListItemIcon>
                                        Logout
                                    </MenuItem>
                                </Menu>
                            </li>}

                        </ul>
                    </div>
                </div>
            </nav>
        </div>

    );
}
