import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import axios from 'axios'
import AOS from 'aos';
import 'aos/dist/aos.css';
import $ from 'jquery'
import Loader from './loader';
class Tour extends Component {
    constructor(props) {
        super(props);
        this.state = {
            tour: [],
            loading: false,
        }
    };
    getData() {
        this.setState({ loading: true })
        axios.post(`http://localhost:3001/api/maintour/topData`)
            .then(res => {
                const tour = res.data;
                this.setState({ tour: tour.tour, loading: false }
                );
            })
            .catch((error) => {
                console.log(error)

            })
        // .finally(() => this.setState({ loading: false }));
    }
    componentDidMount() {
        AOS.init({disable: 'mobile'});
        // this.setState({ loading: true })
        // setTimeout(this.getData(), 10000);
        this.getData();
    }
    componentDidUpdate() {
        $('.post-module').hover(function () {
            $(this).find('.description').stop().animate({
                height: "toggle",
                opacity: "toggle"
            }, 300);
        });
    }
    render() {
        // console.log("this.state.loading:", this.state.loading)
        if (this.state.loading === true) {
            return (
                <section id="tour" style={{ paddingTop: '10%', paddingBottom: '10%' }}>
                    <h1 style={{ textAlign: "center", fontWeight: 'bold', margin: '5%' }}>Hot Tours</h1>
                    <Loader></Loader>
                </section>)
        }
        return (
            <section id="tour" style={{ paddingTop: '10%', paddingBottom: '10%' }}>
                <h1 style={{ textAlign: "center", fontWeight: 'bold', margin: '5%' }}>Hot Tours</h1>
                <div style={{ display: 'flex', flexWrap: 'wrap' }}>

                    {this.state.tour.map(item => (
                        <Link to={`tour/${item.title}`} className="tourBox" data-aos-duration="1500" data-aos="flip-left" key={item.id}>
                            <div >
                                <div className="post-module">
                                    <div className="thumbnail">
                                        <img alt="tour" src={item.image} />
                                    </div>
                                    <div className="post-content">
                                        <div className="category">Photos</div>
                                        <h1 className="title">{item.title}</h1>
                                        {/* https://drive.google.com/uc?export=view&id= */}
                                        {
                                            item.tag ? <h2 className="sub_title" style={{ display: 'flex' }}>Tags:
                                                <div className="tag_box_tour">{item.tag.split(",")[0]}</div>
                                            </h2> : "No tags"
                                        }

                                        <p className="description">{item.description.slice(0, 300)}. . .</p>
                                    </div>
                                </div>
                            </div>
                        </Link>

                    ))}

                    {/* <div data-aos="flip-left" className="tourBox">
                        <div className="post-module">
                            <div className="thumbnail">
                                <img alt="tour" src="https://s3-us-west-2.amazonaws.com/s.cdpn.io/169963/photo-1429043794791-eb8f26f44081.jpeg" />
                            </div>
                            <div className="post-content">
                                <div className="category">Photos</div>
                                <h1 className="title">City Lights in New York</h1>
                                <h2 className="sub_title">The city that never sleeps.</h2>
                                <p className="description">New York, the largest city in the U.S., is an architectural marvel with plenty of historic monuments, magnificent buildings and countless dazzling skyscrapers.</p>
                            </div>
                        </div>
                    </div> */}

                </div>
            </section>
        )
    }

}
export default Tour