// import './App.css';
import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Header from './pages/header';
import AboutUs from './pages/aboutUs';
import NavBar from './pages/navBar'
import Tour from './pages/hotTour'
import Footer from './pages/footer';
import ChildHotTour from './pages/childHotTour';
import NotFound from './pages/notFound';
import Blog from './pages/blog';
import Destination from './pages/destination'
import BlogList from './pages/blogList';
import LoginRegister from './pages/login_register';
const jwt = require('jsonwebtoken')
class App extends Component {
  constructor() {
    super();
    if (localStorage.getItem('token') !== "" && localStorage.getItem('token') != null) {
      this.state = {
        loginStatus: localStorage.getItem('token'),
        user: jwt.decode(localStorage.getItem('token')).username,
      }
    }
    else {
      this.state = {
        loginStatus: localStorage.getItem('token'),
        user: "",
      }
    }

  }
  logout = (e) => {
    // e.preventDefault()
    localStorage.removeItem('token');
    this.setState({ loginStatus: null });
  }
  login = () => {
    // let username = jwt.decode(localStorage.getItem('token')).username
    this.setState({
      loginStatus: localStorage.getItem('token'),
      user: jwt.decode(localStorage.getItem('token')).username,
    });
  }


  // parseJwt(token) {
  //   if (!token) { return; }
  //   const base64Url = token.split('.')[1];
  //   const base64 = base64Url.replace('-', '+').replace('_', '/');
  //   return JSON.parse(window.atob(base64));
  // }

  render() {
    return (
      <div className="App">
        <Router>
          {/* {this.state.loginStatus === null &&  <NavBar />}
          {this.state.loginStatus != null &&  <NavBar2 />}  */}
          <NavBar isLoginIn={this.state.loginStatus} userName={this.state.user} logUserOut={this.logout} />
          <Switch>
            <Route exact path="/">
              <Header />
              <AboutUs />
              <Tour />
            </Route>
            <Route exact path="/sign">
              <LoginRegister logUserIn={this.login} />
            </Route>

            {/* <Route exact path="/sign" component={LoginRegister}></Route> */}
            <Route exact path="/tour" component={Destination} />
            <Route exact path="/tour/:name" component={ChildHotTour} />
            <Route exact path="/blog" component={BlogList} />
            <Route exact path="/blog/:name" component={Blog} />
            <Route component={NotFound} />
          </Switch>
          <Footer />
        </Router>
      </div>


    );
  }

}

export default App;
