import React, { Component } from 'react'
import axios from 'axios'
import 'aos/dist/aos.css';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import Accordion from 'react-bootstrap/Accordion'

class PageTour extends Component {
    constructor(props) {
        super(props);
        this.state = {
            imgLength: 4,
            tour: [],
            childTour: [],
            imageList: [],
            tag: [],
            nav1: null,
            nav2: null,
            day: [],
            paragraph: [],
            info: {
                checkTour: "",
                time: null,
                departAt: null,
                minimum: null,
                fee: null,
                address: null,
                description: null,
                backgroundImage: null,
                stringTag: null,
            },
        };
    }
    
    getAllTourdata() {
        axios.post('/api/maintour/allData')
            .then(res => {
                const tourData = res.data;
                const tour = tourData["tour"];
                const checkTour = this.props.paramID;
                console.log('ten:',checkTour)
                // const titleList = tour.map(item => item.title);
                // const checkTour = titleList.find(element => element === paramName)
                const time = tour.find(info => info.title === checkTour).time
                const departAt = tour.find(info => info.title === checkTour).depart
                const minimum = tour.find(info => info.title === checkTour).minimum
                const fee = tour.find(info => info.title === checkTour).fee
                const address = tour.find(info => info.title === checkTour).address
                const description = tour.find(info => info.title === checkTour).description
                const backgroundImage = tour.find(info => info.title === checkTour).image
                const stringTag = tour.find(info => info.title === checkTour).tag
                const tagArr = stringTag.split(',');
                let tagString = "";
                if (tagArr.length >= 3) {
                    let maxChar = 0;
                    for (let i = 0; i < 4; i++) {
                        maxChar += tagArr[i].length;
                    }
                    if (maxChar >= 30) {
                        tagString = tagArr.slice(0, 4);
                    }
                    else {
                        tagString = tagArr;
                    }
                }
                else {
                    tagString = tagArr;
                }
                this.setState({
                    info: {
                        checkTour: checkTour,
                        time: time,
                        departAt: departAt,
                        minimum: minimum,
                        fee: fee,
                        address: address,
                        description: description,
                        backgroundImage: backgroundImage,
                        stringTag: stringTag,
                    },
                    nav1: this.slider1,
                    nav2: this.slider2,
                    tag: tagString,
                })
            })
            .catch(error => console.log(error));
        
    }

    getChildTourimage() {
        axios.post('/api/childtour/image')
            .then(res => {
                const childTourData = res.data;
                const childTour = childTourData["childTour"];
                childTour.forEach(element => {
                    if (element.title === this.props.paramID) {
                        this.state.imageList.push(element.image);
                    }
                })
                if (this.state.imageList.length <= 4) {
                    this.setState({ imgLength: this.state.imageList.length })
                }
                else {
                    this.setState({ imgLength: 4 })
                }
            })
            .catch(error => console.log(error));
    }

    getChildTourdata() {
        const data = this.props.paramID;
        axios.post('/api/childtour/day', {
            titleTour: data,
        })
            .then(res => {
                const day = res.data;
                this.setState({ day: day.day });
            })
            .catch(error => console.log(error));

    }
    // componentWillMount(){
    //     this.getAllTourdata();
    //     this.getChildTourimage();
    //     this.getChildTourdata();  
    // }
    componentDidMount() {
        //get all tours
        this.getAllTourdata();
        //get all child tour's image
        this.getChildTourimage()
        //get all child tour's day info
        this.getChildTourdata()    
        // this.setState({loading: true})
    }
    render() {
            let settingsMain = {
                slidesToShow: 1,
                slidesToScroll: 1,
                arrows: false,
                fade: true,
                adaptiveHeight: true,
            };
            let settingsThumbs = {
                className: "tour_slider",
                infinite: true,
                centerPadding: "60px",
                slidesToShow: this.state.imgLength,
                swipeToSlide: true,
                focusOnSelect: true,
                arrows: false,
                lazyLoad: "progressive",
            }
           
            return (
    
                <section id="childHotTour">
                    <div className="main_content" style={{
                        backgroundImage: `url(${this.state.info.backgroundImage})`,
                    }}>
                        <div className="shadow_content">
                            <h1 className="title_content">{this.state.info.checkTour}</h1>
    
                            <div className="tour_content">
    
                                <div className="rightcol clearfix">
                                    <div className="banner">
                                        <img className="banner_tour" alt="banner_tour" src={this.state.info.backgroundImage}></img>
                                    </div>
                                    <div className="info_section1">
                                        <div className="info_section1_div">
                                            <div className="info_box"><span>Thời gian: {this.state.info.time}</span></div>
                                            <div className="info_box"><span>Giờ khởi hành: {this.state.info.departAt}</span></div>
                                            <div className="info_box"><span>Số người tối thiểu: {this.state.info.minimum}+</span></div>
    
                                        </div>
                                    </div>
                                    <div className="info_section2">
                                        <div className="info_section2_div">
                                            <div className="highlight_description" id="style-1">
                                                <p className="description_content">{this.state.info.description}</p>
                                            </div>
                                            <div className="fee">
                                                <div className="svg-wrapper">
                                                    <svg height="40" width="300">
                                                        <rect className="shape" height="40" width="300" />
                                                    </svg>
                                                    <div className="text"><p>Giá: {this.state.info.fee}$</p></div>
                                                </div>
                                            </div>
                                            <div className="tag_content">
                                                {this.state.tag.map((item, i) => (
                                                    <div key={i} className="tag_box">{item}</div>))}
                                            </div>
                                        </div>
    
                                    </div>
    
                                </div>
                                <div className="leftcol">
                                    <div className="higlight_section">
                                        <div className="screenshot_holder">
                                            <div className="slider_holder">
                                                <Slider
                                                    {...settingsMain}
                                                    asNavFor={this.state.nav2}
                                                    ref={slider => (this.slider1 = slider)}
                                                >
                                                    {this.state.imageList.map((item, i) => (
                                                        <img key={i} className="tour_main_img" src={item} alt="highlight_image" />))}
                                                </Slider>
                                            </div>
    
                                        </div>
                                        <div className="highlight_strip">
                                            <div className="highlight_strip_scroll">
                                                <Slider
                                                    {...settingsThumbs}
                                                    asNavFor={this.state.nav1}
                                                    ref={slider => (this.slider2 = slider)}
                                                >
                                                    {this.state.imageList.map((item, i) => (
                                                        <div key={i}>
                                                            <img className="tour_img" src={item} alt="highlight_image" />
                                                        </div>
                                                    ))
                                                    }
                                                </Slider>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="dayTripsContent">
                                <Accordion>
                                    {this.state.day.map((item, i) => (
                                        <div key={i}>
                                            <Accordion.Item eventKey={i}>
                                                <Accordion.Header >Day {i + 1}: {item.titleDay}</Accordion.Header>
                                                <Accordion.Body >{item.content.split("\\n").map((subPara, k) => (
                                                    <p key={k}>{subPara}</p>
                                                ))}
    
                                                </Accordion.Body>
                                            </Accordion.Item>
                                        </div>
    
                                    ))}
                                </Accordion>
                            </div>
    
                        </div>
    
                    </div>
    
    
    
                </section>
            );
  
    }
}
export default PageTour