import React, { Component } from 'react'
import axios from 'axios'
import 'aos/dist/aos.css';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Loader from './loader';
import PageTour from './pageTour';
import NotFound from './notFound';

class ChildHotTour extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            info: {
                checkTour: null,
            },
        };
    }

    checkTourdata() {
        // console.log('checkTour :',this.state.info.checkTour)
        this.setState({ loading: true })
        axios.post('/api/maintour/allData')
            .then(res => {
                const tourData = res.data;
                const tour = tourData["tour"];
                const paramName = this.props.match.params.name;
                const titleList = tour.map(item => item.title);
                const checkTour = titleList.find(element => element === paramName)
                // console.log('checkTour:', checkTour)
                this.setState({
                    info: {
                        checkTour: checkTour,
                    },
                })
            })
            .catch(error => console.log(error));
    }
    componentDidMount() {
        //get if tour exist
        this.checkTourdata();
        setTimeout(() => { 
              this.setState({loading: false})
        }, 2000);
    }
    render() {
        console.log('loading: ', this.state.loading)
        console.log('checkTour:', this.state.info.checkTour)
        if (!this.state.info.checkTour && this.state.loading === true) {
            return (
                <section id="childHotTour" style={{ paddingTop: '100px', height: '61vh' }}>
                    <Loader/>
                </section >
            )
        }
        else if (!this.state.info.checkTour && this.state.loading === false) {
            return (
                <section id="childHotTour">
                    <NotFound/>
                </section>
            )

        }
        else {
            return (
                <section id="childHotTour">
                    <PageTour paramID={this.state.info.checkTour} />
                </section>

            );
        }


    }
}
export default ChildHotTour