import React, { Component } from 'react'
// import { Link } from 'react-router-dom';
import axios from 'axios'
import AOS from 'aos';
import 'aos/dist/aos.css';
import Loader from './loader';
import NotFound from './notFound';
import PageBlog from './pageBlog';
class Blog extends Component {
    constructor(props) {
        super(props);
        this.state = {
            blog:[],
            loading: false,
        }
    };
    checkBlogData() {
        this.setState({ loading: true })
        const data = this.props.match.params.name;
        axios.post('/api/blog', {
            blog: data,
        })
            .then(res => {
                const blog = res.data;
                this.setState({ blog: blog.blog });
            })
            .catch(error => console.log(error));
    }
    componentDidMount() {
        AOS.init();
        this.checkBlogData();
        setTimeout(() => { 
            this.setState({loading: false})
      }, 2000);
    }
    render() {
        // console.log('this.state.blog.length',this.state.blog.length)
        // console.log('this.state.loading',this.state.loading)
        if (this.state.blog.length === 0 && this.state.loading === true) {
            return (
                <section id="tour" style={{ paddingTop: '100px', height: '61vh' }}>
                   <Loader/>
                </section >
            )
        }
        else if (this.state.blog.length === 0 && this.state.loading === false)
        {
            return (
                <section id="tour">
                    <NotFound/>
                </section>
            )
        }
        else {
            return (
                <section id="tour" className="title_travelblogs">
                    <PageBlog paramID={this.state.blog}/>
                </section>
            )
        }
       
    }

}
export default Blog