import React, { useEffect, useState } from "react";
import Axios from 'axios';
import { useHistory } from 'react-router';
import { useToken } from '../auth/useToken';
// import { useLocalStorage } from "../auth/useLocalStorage";
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
// import Link from '@mui/material/Link';
// import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { Alert } from "@mui/material";
// import Snackbar from '@mui/material/Snackbar';

const theme = createTheme();

export default function SignIn(props) {

    const history = useHistory();
    const [checked, setChecked] = React.useState(true);
    const [userEmail, setUserEmail] = useState("")
    const [userPassword, setUserPassword] = useState("")
    const [token, setToken] = useToken();
    const [loginStatus, setLoginStatus] = useState("");
    const [open, setOpen] = React.useState(false);
    useEffect(() => {
        if (localStorage.checkbox && localStorage.checkbox !== "") {
            setUserEmail(localStorage.userEmail);
            setUserPassword(localStorage.userPassword);
        }
        else {
            setUserEmail("");
            setUserPassword("");
        }

    }, [])
    useEffect(() => {
        if (loginStatus) {
            setTimeout(() => {
                setLoginStatus("");
            }, 2000);
        }
    }, [loginStatus]);


    const handleSubmit = async (event) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        console.log(checked)
        if (checked === true) {
            localStorage.userEmail = data.get('email');
            localStorage.userPassword = data.get('password');
            localStorage.checkbox = checked;
        }
        else {
            localStorage.userEmail = "";
            localStorage.userPassword = "";
            localStorage.checkbox = "";
        }
        // eslint-disable-next-line no-console
        // console.log({
        //     email: data.get('email'),
        //     password: data.get('password'),
        // });

        await Axios.post("http://localhost:3001/api/login", {
            email: data.get('email'),
            password: data.get('password'),
        }, {
            // headers: {
            //     'Content-Type': 'application/json',
            //     'Access-Control-Allow-Origin': '*',
            // }
        }).then((response) => {
            if (response.data.message) {
                setLoginStatus(response.data.message)
            }
            else {
                const { token } = response.data;
                setToken(token);
                history.push('/');
                props.login();
            }
        }).catch(error => {
            console.log('error:', error)
        });
    };

    const handleChange = (event) => {
        setChecked(event.target.checked);
    };
    const handleClick = () => {
        setOpen(true);
    };
    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpen(false);
    };

    return (
        <ThemeProvider theme={theme}>
            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <Box
                    sx={{
                        marginTop: 8,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >
                    <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                        <LockOutlinedIcon />
                    </Avatar>
                    <Typography component="h1" variant="h5">
                        Sign in
                    </Typography>
                    <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            id="email"
                            label="Email Address"
                            name="email"
                            autoComplete="email"
                            autoFocus
                            value={userEmail}
                            onChange={(e) => setUserEmail(e.target.value)}
                        />
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            name="password"
                            label="Password"
                            type="password"
                            id="password"
                            autoComplete="current-password"
                            value={userPassword}
                            onChange={(e) => setUserPassword(e.target.value)}
                        />
                        <FormControlLabel
                            control={<Checkbox checked={checked} value="remember" color="primary" onChange={handleChange} />}
                            label="Remember me"
                        />
                        {/* <input type="checkbox" value="lsRememberMe" id="rememberMe"/> 
                        <label for="rememberMe"> Remember me</label> */}

                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            sx={{ mt: 3, mb: 2 }}
                        >
                            Sign In
                        </Button>
                        {/* <Grid container>
                            <Grid item xs>
                                <Link href="#" variant="body2">
                                    Forgot password?
                                </Link>
                            </Grid>
                            <Grid item>
                                <Link href="#" variant="body2">
                                    {"Don't have an account? Sign Up"}
                                </Link>
                            </Grid>
                        </Grid> */}
                    </Box>
                </Box>
                <div style={{ position: "inherit", width: "100%", textAlign: 'center' }}>
                    {loginStatus === "Wrong email/password" && <Alert severity="error">{loginStatus}</Alert>}
                    {/* {loginStatus != "Wrong email/password" && <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
                        <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
                            This is a success message!
                        </Alert>
                    </Snackbar>
                    } */}
                </div>
            </Container>
        </ThemeProvider>
    );
}