import React, { useState, useEffect } from "react";
import Axios from 'axios';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
// import Link from '@mui/material/Link';
// import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { Alert } from "@mui/material";
import { createTheme, ThemeProvider } from '@mui/material/styles';

const theme = createTheme();

export default function SignUp(event) {
    const [password, setPasswordReg] = useState("");
    const [confirmPassword, setConfirmPasswordReg] = useState("");
    const [registerStatus, setRegisterStatus] = useState("");
    // console.log('password:', password)
    // console.log('confirmPassword:', confirmPassword)
    useEffect(() => {
        if (registerStatus) {
            setTimeout(() => {
                setRegisterStatus("");
            }, 2000);
        }
    }, [registerStatus]);
    const handleSubmit = async (event) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        // eslint-disable-next-line no-console
        // console.log({
        //     email: data.get('email'),
        //     password: data.get('password'),
        // });
        await Axios.post("/api/register", {
            username: data.get('username'),
            email: data.get('email'),
            password: data.get('password'),
        }).then((response) => {
            if (response.data.message) {
                setRegisterStatus(response.data.message)
            }
            else {
                setRegisterStatus("Successfully creating new account")
            }
            // console.log(response);
        }).catch(error => {
            console.log(error)
        });
    };

    const checkValidation = (event) => {
        event.preventDefault();
        let email = document.getElementById('email').value;
        let emailRegExp = new RegExp(/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/)
        console.log(email)
        let password = document.getElementById('password').value;
        let confirmPassword = document.getElementById('confirm-password').value;
        if (emailRegExp.test(email)) {
            if (password !== confirmPassword) {
                setRegisterStatus("Password not mataching")
            }
            else if (!password) {
                setRegisterStatus("Please fill in password")
            }
            else {
                handleSubmit(event);
            }
        }
        else {
            setRegisterStatus("Invalid email")
        }

    }
    return (

        <ThemeProvider theme={theme}>
            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <Box
                    sx={{
                        marginTop: 8,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >
                    <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                        <LockOutlinedIcon />
                    </Avatar>
                    <Typography component="h1" variant="h5">
                        Sign up
                    </Typography>
                    <Box component="form" noValidate onSubmit={checkValidation} sx={{ mt: 1 }}>
                        <TextField
                            autoComplete="given-name"
                            name="username"
                            required
                            fullWidth
                            id="username"
                            label="Username"
                            autoFocus
                        />
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            type="email"
                            id="email"
                            label="Email Address"
                            name="email"
                            autoComplete="email"
                            // inputProps={{ pattern: "[a-z]" }}
                        // onChange={(e) => setEmailReg(e.target.value)}
                        />
                        <TextField
                            error={password !== confirmPassword}
                            margin="normal"
                            required
                            fullWidth
                            name="password"
                            label="Password"
                            type="password"
                            id="password"
                            autoComplete="new-password"
                            onChange={(e) => setPasswordReg(e.target.value)}
                        />
                        <TextField
                            error={password !== confirmPassword}
                            margin="normal"
                            required
                            fullWidth
                            name="confirm-password"
                            label="Confirm-password"
                            type="password"
                            id="confirm-password"
                            autoComplete="new-password"
                            onChange={(e) => setConfirmPasswordReg(e.target.value)}
                        />
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            sx={{ mt: 3, mb: 2 }}
                        // onClick={register}
                        >
                            Sign Up
                        </Button>
                        {/* <Grid container justifyContent="flex-end">
                            <Grid item>
                                <Link href="#" variant="body2">
                                    Already have an account? Sign in
                                </Link>
                            </Grid>
                        </Grid> */}
                    </Box>
                </Box>
                <div style={{ position: "inherit", width: "100%", textAlign: 'center' }}>
                    {registerStatus === "User existed" && <Alert severity="error">{registerStatus}</Alert>}
                    {registerStatus === "Password not mataching" && <Alert severity="error">{registerStatus}</Alert>}
                    {registerStatus === "Invalid email" && <Alert severity="warning">{registerStatus}</Alert>}
                    {registerStatus === "Please fill in password" && <Alert severity="warning">{registerStatus}</Alert>}
                    {registerStatus === "Successfully creating new account" && <Alert severity="success">{registerStatus}</Alert>}
                </div>
            </Container>
        </ThemeProvider>

    );
}